.inputRounded .MuiOutlinedInput-root {
    border-radius: 20px;
}

.backGround_search {
    border-style: solid;
    border-color: #2fca3c;
}

.InputLabel {
    background-color: #000000 !important;
}

.title-brand-name {
    display: -webkit-box;
    height: 36px;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    -webkit-line-clamp: 2;
    line-height: 18px;
}

.about-brand {
    display: -webkit-box;
    height: 80px;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    -webkit-line-clamp: 5;
    line-height: 16px;
}